import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState, AppThunk } from '../../app/store';
import { fetchQuote } from './quoteAPI';

export interface QuoteState {
  quote?: {
    id: string,
    title: Record<string, string>,
    context: Record<string, string>,
    language: [string],
    tags: Record<string, [string]>,
    author: Record<string, string>,
    sources: [string],
    contextAuthors: [string],
    books: [string],
    createdAt: string,
    updatedAt: string,
  }
  status: 'idle' | 'loading' | 'failed';
}

const initialState: QuoteState = {
  status: 'idle',
};

export interface IFetchQuoteAsync {
  lang: string
}

export const fetchQuoteAsync = createAsyncThunk(
  'quote/fetchQuote',
  async (props: IFetchQuoteAsync) => {
    const response = await fetchQuote(props);
    return response.data?.quote;
  }
);

export const quoteSlice = createSlice({
  name: 'quote',
  initialState,
  reducers: {
    // increment: (state) => {
    //   // Redux Toolkit allows us to write "mutating" logic in reducers. It
    //   // doesn't actually mutate the state because it uses the Immer library,
    //   // which detects changes to a "draft state" and produces a brand new
    //   // immutable state based off those changes
    //   state.value += 1;
    // },
    // decrement: (state) => {
    //   state.value -= 1;
    // },
    // // Use the PayloadAction type to declare the contents of `action.payload`
    // incrementByAmount: (state, action: PayloadAction<number>) => {
    //   state.value += action.payload;
    // },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(fetchQuoteAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchQuoteAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.quote = action.payload;
      });
  },
});

export const {  } = quoteSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const selectQuoteState = (state: RootState) => state.quote;
export const selectQuote = (state: RootState) => state.quote.quote;

// We can also write thunks by hand, which may contain both sync and async logic.
// Here's an example of conditionally dispatching actions based on current state.
// export const incrementIfOdd = (amount: number): AppThunk => (
//   dispatch,
//   getState
// ) => {
//   const currentValue = selectTitle(getState());
//   if (currentValue % 2 === 1) {
//     dispatch(incrementByAmount(amount));
//   }
// };

export default quoteSlice.reducer;
