import React, { useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { selectQuote } from '../quote/quoteSlice';
import {
  editQuoteAsync,
  selectQuoteFormState,
  addQuoteAsync,
} from "./formSlice";
import { SHARED_MESSAGES } from '../../lang/messages.js';
import { useHistory } from "react-router-dom";
import { useForm } from 'react-hook-form';
import styles from './Form.module.css';
import { set, get, isEmpty, cloneDeep } from 'lodash'
import Loading from '../../common/Loading';

export function QuoteForm() {
  const quoteFormState = useAppSelector(selectQuoteFormState);
  const dispatch = useAppDispatch();
  const history = useHistory();

  let action;;
  switch (history.location.pathname) {
    case "/edit":
      action = "EDIT";
      // action = SHARED_MESSAGES.edit();
      break;
    case "/add":
      action = "ADD";
      // action = SHARED_MESSAGES.send();
      break;
    default:
      break;
  }

  const quote = useAppSelector(selectQuote);
  const [data, setData] = useState(action === "EDIT" ? quote : {})

  const handleOnChange = (path, e, isArray) => {
    console.log('path: ', path)
    console.log('data: ', data)
    console.log('e.target.value: ', e.target.value)
    let newDate = cloneDeep(data)
    
    if (isArray){
      let current = e.target.value.split(', ')
      // console.log('current: ', current)
      set(newDate, path, current)
    } else {
      set(newDate, path, e.target.value)
    }
    console.log('newDate: ', newDate)
    setData(newDate)
  }

  const getFormatedObject = data => ({
    title: {
      ru: data["Title (Russian)"],
      he: data["Title (Hebrew)"],
      en: data["Title (English)"],
    },
    context: {
      ru: data["Context (Russian)"],
      he: data["Context (Hebrew)"],
      en: data["Context (English)"],
    },
    author: {
      ru: data["Author (Russian)"],
      he: data["Author (Hebrew)"],
      en: data["Author (English)"],
    },
    books: data["Books"].split(", "),
    language: data["Languages"].split(", "),
    contextAuthors: data["Context Authors"].split(", "),
    sources: data["Sources"].split(", "),
    tags: {
      ru: data["Tags (Russian)"].split(", "),
      he: data["Tags (Hebrew)"].split(", "),
      en: data["Tags (English)"].split(", "),
    },
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const onSubmit = (data) => {
    const obj = getFormatedObject(data) 
    console.log('obj: ', obj)
    if (action === 'EDIT'){
    dispatch(
      editQuoteAsync({
        id: quote!.id,
        quote: obj,
      })
    );
    } else {
      dispatch(
        addQuoteAsync({
          quote: obj,
        })
      );
    }
  };

  const getForm = () => {
      return (
        <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
          <Input
            label="Title (Russian)"
            register={register}
data={data}
            path='title.ru'
            handleOnChange={handleOnChange}
          />
          <Input
            label="Title (Hebrew)"
            register={register}
data={data}
            path='title.he'
            handleOnChange={handleOnChange}
          />
          <Input
            label="Title (English)"
            register={register}
data={data}
            path='title.en'
            handleOnChange={handleOnChange}
          />

          <Textarea
            label="Context (Russian)"
            register={register}
data={data}
            path='context.ru'
            handleOnChange={handleOnChange}
          />
          <Textarea
            label="Context (Hebrew)"
            register={register}
data={data}
            multiline={true}
            path='context.he'
            handleOnChange={handleOnChange}
          />
          <Textarea
            label="Context (English)"
            register={register}
data={data}
            path='context.en'
            handleOnChange={handleOnChange}
          />

          <Input
            label="Author (Russian)"
            register={register}
data={data}
            path='author.ru'
            handleOnChange={handleOnChange}
          />
          <Input
            label="Author (Hebrew)"
            register={register}
data={data}
            path='author.he'
            handleOnChange={handleOnChange}
          />
          <Input
            label="Author (English)"
            register={register}
data={data}
            path='author.en'
            handleOnChange={handleOnChange}
          />

          <Input
            label="Books"
            register={register}
data={data}
            path='books'
            isArray={true}
            handleOnChange={handleOnChange}
          />
          <Input
            label="Context Authors"
            register={register}
data={data}
            path='contextAuthors'
            isArray={true}
            handleOnChange={handleOnChange}
          />
          <Input
            label="Languages"
            register={register}
data={data}
            path='language'
            isArray={true}
            handleOnChange={handleOnChange}
          />
          <Input
            label="Sources"
            register={register}
data={data}
            path='sources'
            isArray={true}
            handleOnChange={handleOnChange}
          />

          <Input
            label="Tags (Russian)"
            register={register}
data={data}
            path='tags.ru'
            isArray={true}
            handleOnChange={handleOnChange}
          />
          <Input
            label="Tags (Hebrew)"
            register={register}
data={data}
            path='tags.he'
            isArray={true}
            handleOnChange={handleOnChange}
          />
          <Input
            label="Tags (English)"
            register={register}
data={data}
            path='tags.en'
            isArray={true}
            handleOnChange={handleOnChange}
          />

          <input type="submit" value={action} />
        </form>
      );
  };

  return (
    quoteFormState.editStatus === 'loading' ? <Loading/> : getForm()
  )
}


const Input = ({
  label,
  register,
  data,
  path,
  isArray = false,
  handleOnChange,
}) => (
  <div className={styles.inputContainer}>
    <label>{label}</label>
    <input
      {...register(label)}
      value={isArray ? get(data, path, []).join(", ") : get(data, path, "")}
      onChange={(e) => handleOnChange(path, e, isArray)}
    />
  </div>
);

const Textarea = ({ label, register, data, multiline = false, path = 'data.title.ru', handleOnChange }) => (
  <div className={styles.textareaContainer}>
    <label>{label}</label>
    <textarea
      {...register(label)}
      value={get(data, path, "")}
      rows={6}
      multiline={multiline}
      onChange={(e) => handleOnChange(path, e, false)}
    />
  </div>
);