import React, { useEffect } from 'react';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { selectQuoteState, fetchQuoteAsync } from './quoteSlice';
import styles from './Quote.module.css';
import Chip from '@material-ui/core/Chip';
import { selectLang } from "../contextMenu/contextMenuSlice";
import { SHARED_MESSAGES } from '../../lang/messages.js';
import { useHistory } from "react-router-dom";
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import Loading from '../../common/Loading';

export function Quote() {
  const quoteState = useAppSelector(selectQuoteState);
  const dispatch = useAppDispatch();
  const history = useHistory();

  const selectedLang = useAppSelector(selectLang);

  const handleFetchQuote = () => {
    dispatch(fetchQuoteAsync({lang: selectedLang}))
  }

  const handleEditQuote = () => {
    console.log('handleEditQuote')
    history.push('/edit');
  }

  useEffect(() => {
    dispatch(fetchQuoteAsync({lang: selectedLang}))
  }, [])

  const getQuoteData = () => {
    return (
      <div className={styles.wrapper}>
        <p className={styles.title}> {quoteState.quote?.title[selectedLang]}</p>
        <p className={styles.author}>
          {" "}
          {quoteState.quote?.author[selectedLang]}{" "}
        </p>
        <div
          dangerouslySetInnerHTML={{
            __html: quoteState.quote?.context[selectedLang] || "",
          }}
          className={`${styles.context} ${
            selectedLang == "he" ? styles.contextRtl : styles.contextLtl
          }`}
        ></div>
        <div className={styles.tagsContainer}>
          {quoteState.quote?.tags[selectedLang].map((tag, index) => {
            return <Chip key={tag} label={tag} />;
          })}
        </div>

        <div className={styles.buttonContainer}>
          <ButtonGroup
            size="large"
            className={styles.buttonGroup}
            color="primary"
            aria-label="large quote actions"
          >
            <Button onClick={handleFetchQuote}>
              {SHARED_MESSAGES.next()}
            </Button>
            <Button onClick={handleEditQuote}>
              {SHARED_MESSAGES.edit()}
            </Button>
          </ButtonGroup>
        </div>
      </div>
    );
  }
  return (
    <div className={styles.container}>
      {quoteState.status === "loading" ? <Loading /> : getQuoteData()}
    </div>
  );
}
