import axios from "../../app/axios";
import { IFetchQuoteAsync } from "./quoteSlice";

export function fetchQuote(props: IFetchQuoteAsync) {
  let query = "?";
  Object.keys(props).forEach((key) => {
    query += `${key}=${props[key]}`;
  });
  console.log(query);
  return axios.get("/quotes/random" + query);
}
